<template>
    <div class="section-container">
        <v-row class="signin">
            <v-col cols="7" class="left">
                <h1>Welcome to my site</h1>
            </v-col>
            <v-col cols="5" class="right">
                <v-tabs v-model="tab" background-color="transparent" color="white" grow class="v=tabs">
                    <v-tabs-slider color="yellow"></v-tabs-slider>
                    <v-tab class="v-tab" :key="login-tab">登录</v-tab>
                    <v-tab class="v-tab" :key="register-tab">注册</v-tab>
                </v-tabs>
                <v-tabs-items v-model="tab" class="v-tabs-items">
                    <v-tab-item :key="login-item">
                        <v-form class="v-form" @submit.prevent="submit">
                            <v-text-field class="field" v-model="login.username" :error-messages="errors" label="用户名" required outlined
                                filled background-color="white" color="darkThemeColor"></v-text-field>
                            <v-text-field class="field" v-model="login.password" :error-messages="errors" label="密码"
                                :append-icon="showPass1 ? 'mdi-eye' : 'mdi-eye-off'" @click:append="showPass1 = !showPass1"
                                required outlined filled :type="showPass1 ? 'text' : 'password'"
                                background-color="white" color="darkThemeColor">
                            </v-text-field>
                        </v-form>
                    </v-tab-item>
                    <v-tab-item :key="register-item">
                        <v-form class="v-form" @submit.prevent="submit">
                            <v-text-field class="field" v-model="register.username" :error-messages="errors" label="用户名" required outlined
                                filled background-color="white" color="darkThemeColor" dense></v-text-field>
                            <v-text-field class="field" v-model="register.student_id" :error-messages="errors" label="队长学号" required outlined
                                filled background-color="white" color="darkThemeColor" dense></v-text-field>
                            <v-text-field class="field" v-model="register.email" :error-messages="errors" label="邮箱" required outlined
                                filled  background-color="white" color="darkThemeColor" dense></v-text-field>
                            <v-text-field class="field" v-model="register.phone" :error-messages="errors" label="队长手机号" required outlined
                                filled  background-color="white" color="darkThemeColor" dense></v-text-field>
                            <v-text-field class="field" v-model="register.names" :error-messages="errors" label="队员姓名" required outlined
                                filled  background-color="white" color="darkThemeColor" dense
                                hint="以分号分隔，队长姓名为首" placeholder="例：张三；李四；王五"></v-text-field>
                            <v-text-field class="field" v-model="register.password" :error-messages="errors" label="密码"
                                :append-icon="showPass2 ? 'mdi-eye' : 'mdi-eye-off'" @click:append="showPass2 = !showPass2"
                                required outlined filled :type="showPass2 ? 'text' : 'password'"
                                background-color="white" color="darkThemeColor" dense>
                            </v-text-field> 
                        </v-form>
                    </v-tab-item>
                </v-tabs-items>
                <div class="btn-div text-center">
                    <v-btn class="signin-btn" type="submit" rounded color="white" dark>
                        完成
                    </v-btn>
                </div>
            </v-col>
        </v-row>
    </div>
</template>

<script>
    export default {
        name: "LoginPage",
        components: {
        },
        data: () => ({
            email: '',
            login: {
                username: null,
                password: null
            },
            register: {
                username: null,
                password: null,
                phone: null,
                email: null,
                names: null,
                student_id: null
            },
            showPass1: false,
            showPass2: false,
            tab: null,
            themeColor: '#30ac7c',
            darkThemeColor: '#1d6046'
        }),
        computed: {
        },
        methods: {
            async submit() {
                const valid = true //await this.$refs.observer.validate()
                if (valid) {
                    if (this.tab == 0)
                        this.login_post(this.login) // action to login
                    else this.register_post(this.register)
                }
            },
            clear() {
                // you can use this method to clear login form
                this.email = ''
                this.password = null
                //this.$refs.observer.reset()
            }
        }
    }
</script>

<style lang="scss">
    $themeColor: #30ac7c;
    $darkThemeColor: #1d6046;
    .section-container {
        background: #fff;
        width: 100%;
        box-shadow: 0 0 1px 1px rgba(#000000, 0.1);
        box-sizing: border-box;

        .signin {
            padding: 0;
            margin: 0 auto;
            min-height: 600px;
            box-shadow: 0 0 1px 1px rgba(#000000, 0.1);

            .left {
                padding: 30px;
                justify-content: center;
                align-items: center;
                box-sizing: border-box;
                display: flex;
                color: $themeColor;
                background-color: #f9f9f9;
            }

            .right {
                padding: 5vh 5vw;
                box-sizing: border-box;
                background: rgba($themeColor, 0.6);
                color: #fff;

                .v-tabs {
                    font-size: 90px;
                    .v-tab {
                        font-size: 1.2vw;
                        letter-spacing: 1vw;
                        //font-weight: bolder;
                    }
                }

                .v-tabs-items {
                    padding-top: 25px;
                    background-color: transparent;
                }

                .v-form {
                    background-color: transparent;
                }

                .field {
                    background-color: transparent;
                }

                h2 {
                    text-align: center;
                    margin: 30px 0;
                }
                .btn-div {
                    padding: 0 20px;
                }

                .signin-btn {
                    width: 100%;
                    color: $themeColor;
                }
            }
        }
    }
</style>